.quicktab {
  display: flex;
  flex-direction: column;
  /*  面板容器占满剩余高度  */
}
.quicktab *,
.quicktab ::after,
.quicktab ::before {
  box-sizing: border-box;
}
.quicktab .tab-bar {
  display: flex;
  flex-shrink: 0;
  padding-left: 0;
  margin: 0;
  list-style: none;
  border: 1px solid #dee2e6;
}
.quicktab .tab-bar.order-bottom {
  order: 2;
}
.quicktab .tab-bar li button {
  display: flex;
  flex-shrink: 0;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 8px 16px;
  font-size: 16px;
  color: #212529;
  cursor: pointer;
  background: #fff;
  border: 0;
  border-radius: 0;
  outline: none;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out;
}
.quicktab .tab-bar li button.tab-control-prev, .quicktab .tab-bar li button.tab-control-refresh {
  border-right: 1px solid #dee2e6;
}
.quicktab .tab-bar li button.tab-control-next, .quicktab .tab-bar li button.tab-control-more, .quicktab .tab-bar li button.tab-control-full {
  border-left: 1px solid #dee2e6;
}
.quicktab .tab-bar li.scroll {
  display: flex;
  flex-grow: 1;
  overflow: auto;
}
.quicktab .tab-bar li.scroll::-webkit-scrollbar {
  display: none;
}
.quicktab .tab-bar li.scroll button {
  position: relative;
  max-width: 180px;
  border-right: 1px solid #dee2e6;
}
.quicktab .tab-bar li.scroll button::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 1px;
  content: "";
  background-color: #7431fa;
  border-radius: 0;
  transition: all 0.3s;
}
.quicktab .tab-bar li.scroll button:hover {
  color: #7431fa;
}
.quicktab .tab-bar li.scroll button:hover::after {
  width: 100%;
}
.quicktab .tab-bar li.scroll button.active {
  color: #7431fa;
}
.quicktab .tab-bar li.scroll button.active::after {
  width: 100%;
}
.quicktab .tab-bar li.scroll button span {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.quicktab .tab-bar li.scroll button svg {
  flex-shrink: 0;
  margin-left: 8px;
  font-size: 12px;
  color: #c2c2c2;
  border-radius: 50%;
}
.quicktab .tab-bar li.scroll button svg:hover {
  color: #fff;
  background-color: #ff5722;
}
.quicktab .tab-body {
  position: relative;
  flex-grow: 1;
  overflow: hidden;
}
.quicktab .tab-body .tab-body-item {
  position: absolute;
  top: -200%;
  width: 100%;
  height: 100%;
}
.quicktab .tab-body .tab-body-item.active {
  position: unset;
  left: unset;
}
.quicktab .tab-body .tab-body-item iframe {
  display: block;
  width: 100%;
  height: 100%;
  border: 0;
}
.quicktab .tab-body .tab-body-item .mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.quicktab .tab-body .tab-body-item .mask .mask-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: #fff;
}

@media (max-width: 400.98px) {
  .quicktab .tab-control-refresh {
    display: none !important;
  }
  .quicktab .tab-control-prev {
    display: none !important;
  }
  .quicktab .tab-control-next {
    display: none !important;
  }
  .quicktab .tab-control-full {
    display: none !important;
  }
}
.quicktab-dropdown-menu {
  display: inline-block;
  width: 9rem;
  padding: 0;
  margin: 0;
  overflow: hidden;
  list-style: none;
  border-radius: 0.375rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
}
.quicktab-dropdown-menu li {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  font-weight: 400;
  color: #212529;
  cursor: pointer;
  user-select: none;
}
.quicktab-dropdown-menu li:hover, .quicktab-dropdown-menu li:focus {
  color: #212529;
  background-color: #f8f9fa;
}
.quicktab-dropdown-menu li.active, .quicktab-dropdown-menu li:active {
  color: #fff;
  background-color: #7431fa;
}
.quicktab-dropdown-menu li > span {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.quicktab-loaders {
  position: relative;
  box-sizing: border-box;
  width: 64px;
  height: 64px;
  color: #7431fa;
}
.quicktab-loaders > div {
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  display: inline-block;
  float: none;
  width: 64px;
  height: 64px;
  background-color: currentcolor;
  border: 0 solid currentcolor;
  border-radius: 100%;
  opacity: 0;
  animation: ball-scale-multiple 1s 0s linear infinite;
}
.quicktab-loaders > div:nth-child(2) {
  animation-delay: 0.2s;
}
.quicktab-loaders > div:nth-child(4) {
  animation-delay: 0.4s;
}

@keyframes ball-scale-multiple {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  5% {
    opacity: 0.75;
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}